import React, { useState, useEffect } from 'react';
import { 
  UserPlusIcon, 
  MagnifyingGlassIcon,
  CheckCircleIcon,
  XCircleIcon,
  PencilSquareIcon,
  TrashIcon
} from '@heroicons/react/24/outline';

// نوع بيانات المستخدم
interface User {
  id: string;
  name: string;
  email: string;
  type: 'admin' | 'provider' | 'client';
  avatar: string;
  status: 'نشط' | 'معلق' | 'محظور';
  isVerified: boolean;
  createdAt: string;
  lastLogin: string;
}

// بيانات افتراضية للعرض التوضيحي
const mockUsers: User[] = [
  {
    id: '1',
    name: 'أحمد محمد',
    email: 'ahmed@example.com',
    type: 'provider',
    avatar: '/images/avatars/provider-avatar.jpg',
    status: 'نشط',
    isVerified: true,
    createdAt: '2023/11/15',
    lastLogin: '2024/05/12'
  },
  {
    id: '2',
    name: 'سارة علي',
    email: 'sara@example.com',
    type: 'client',
    avatar: '/images/avatars/client-avatar.jpg',
    status: 'نشط',
    isVerified: false,
    createdAt: '2024/01/23',
    lastLogin: '2024/05/11'
  },
  {
    id: '3',
    name: 'محمد أحمد',
    email: 'mohamed@example.com',
    type: 'provider',
    avatar: '/images/avatars/provider-avatar.jpg',
    status: 'نشط',
    isVerified: true,
    createdAt: '2023/10/05',
    lastLogin: '2024/05/10'
  },
  {
    id: '4',
    name: 'فاطمة خالد',
    email: 'fatima@example.com',
    type: 'client',
    avatar: '/images/avatars/client-avatar.jpg',
    status: 'معلق',
    isVerified: false,
    createdAt: '2024/02/10',
    lastLogin: '2024/04/28'
  },
  {
    id: '5',
    name: 'خالد عبدالله',
    email: 'khaled@example.com',
    type: 'provider',
    avatar: '/images/avatars/provider-avatar.jpg',
    status: 'نشط',
    isVerified: true,
    createdAt: '2023/09/15',
    lastLogin: '2024/05/09'
  },
  {
    id: '6',
    name: 'مريم أحمد',
    email: 'mariam@example.com',
    type: 'client',
    avatar: '/images/avatars/client-avatar.jpg',
    status: 'محظور',
    isVerified: false,
    createdAt: '2023/12/20',
    lastLogin: '2024/03/15'
  },
  {
    id: '7',
    name: 'عمر محمد',
    email: 'omar@provider.com',
    type: 'provider',
    avatar: '/images/avatars/provider-avatar.jpg',
    status: 'نشط',
    isVerified: true,
    createdAt: '2023/08/10',
    lastLogin: '2024/05/08'
  },
  {
    id: '8',
    name: 'ليلى عبدالرحمن',
    email: 'laila@example.com',
    type: 'client',
    avatar: '/images/avatars/client-avatar.jpg',
    status: 'نشط',
    isVerified: false,
    createdAt: '2024/03/05',
    lastLogin: '2024/05/07'
  },
  {
    id: '9',
    name: 'يوسف علي',
    email: 'yousef@provider.com',
    type: 'provider',
    avatar: '/images/avatars/provider-avatar.jpg',
    status: 'معلق',
    isVerified: true,
    createdAt: '2023/07/25',
    lastLogin: '2024/04/20'
  },
  {
    id: '10',
    name: 'نورة محمد',
    email: 'noura@example.com',
    type: 'client',
    avatar: '/images/avatars/client-avatar.jpg',
    status: 'نشط',
    isVerified: false,
    createdAt: '2024/01/30',
    lastLogin: '2024/05/06'
  },
];

export default function Users() {
  const [users, setUsers] = useState<User[]>([]);
  const [filteredUsers, setFilteredUsers] = useState<User[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedType, setSelectedType] = useState<string>('all');
  const [selectedStatus, setSelectedStatus] = useState<string>('all');
  const [selectedVerified, setSelectedVerified] = useState<string>('all');
  const [currentPage, setCurrentPage] = useState(1);
  const usersPerPage = 8;

  useEffect(() => {
    // محاكاة جلب البيانات من الخادم
    const fetchData = async () => {
      try {
        setIsLoading(true);
        // تأخير لمحاكاة جلب البيانات
        await new Promise(resolve => setTimeout(resolve, 800));
        setUsers(mockUsers);
        setFilteredUsers(mockUsers);
      } catch (error) {
        console.error('Error fetching users:', error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    // تطبيق الفلاتر على المستخدمين
    let result = [...users];

    // البحث
    if (searchTerm) {
      result = result.filter(
        user =>
          user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
          user.email.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    // فلتر النوع
    if (selectedType !== 'all') {
      result = result.filter(user => user.type === selectedType);
    }

    // فلتر الحالة
    if (selectedStatus !== 'all') {
      result = result.filter(user => user.status === selectedStatus);
    }

    // فلتر التوثيق
    if (selectedVerified !== 'all') {
      const isVerified = selectedVerified === 'verified';
      result = result.filter(user => user.isVerified === isVerified);
    }

    setFilteredUsers(result);
    setCurrentPage(1); // إعادة التعيين للصفحة الأولى عند تغيير الفلاتر
  }, [users, searchTerm, selectedType, selectedStatus, selectedVerified]);

  // حساب المستخدمين المعروضين حاليًا
  const indexOfLastUser = currentPage * usersPerPage;
  const indexOfFirstUser = indexOfLastUser - usersPerPage;
  const currentUsers = filteredUsers.slice(indexOfFirstUser, indexOfLastUser);
  const totalPages = Math.ceil(filteredUsers.length / usersPerPage);

  // وظائف التنقل بين الصفحات
  const paginate = (pageNumber: number) => setCurrentPage(pageNumber);
  const goToNextPage = () => setCurrentPage(prev => Math.min(prev + 1, totalPages));
  const goToPreviousPage = () => setCurrentPage(prev => Math.max(prev - 1, 1));

  if (isLoading) {
    return (
      <div className="py-12 flex justify-center">
        <div className="inline-flex items-center">
          <svg className="animate-spin h-5 w-5 text-indigo-600 ml-3" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
          <span className="font-medium text-indigo-600">جاري تحميل المستخدمين...</span>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* أدوات الفلترة والبحث */}
      <div className="bg-white p-6 rounded-lg shadow">
        <div className="flex flex-col md:flex-row md:justify-between md:items-center space-y-4 md:space-y-0">
          <div className="flex items-center">
            <h2 className="text-xl font-semibold text-gray-800">المستخدمين</h2>
            <span className="mr-2 text-sm text-gray-500">({filteredUsers.length} مستخدم)</span>
          </div>
          
          <button className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
            <UserPlusIcon className="h-4 w-4 ml-2" aria-hidden="true" />
            إضافة مستخدم
          </button>
        </div>

        <div className="mt-6 grid grid-cols-1 md:grid-cols-4 gap-4">
          {/* حقل البحث */}
          <div className="relative">
            <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
              <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" aria-hidden="true" />
            </div>
            <input
              type="text"
              name="search"
              placeholder="بحث باسم المستخدم أو البريد الإلكتروني"
              value={searchTerm}
              onChange={e => setSearchTerm(e.target.value)}
              className="block w-full border-gray-300 rounded-md pr-10 focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            />
          </div>

          {/* فلتر النوع */}
          <div>
            <select
              value={selectedType}
              onChange={e => setSelectedType(e.target.value)}
              className="block w-full border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            >
              <option value="all">جميع المستخدمين</option>
              <option value="admin">مدير</option>
              <option value="provider">مزود خدمة</option>
              <option value="client">عميل</option>
            </select>
          </div>

          {/* فلتر الحالة */}
          <div>
            <select
              value={selectedStatus}
              onChange={e => setSelectedStatus(e.target.value)}
              className="block w-full border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            >
              <option value="all">جميع الحالات</option>
              <option value="نشط">نشط</option>
              <option value="معلق">معلق</option>
              <option value="محظور">محظور</option>
            </select>
          </div>

          {/* فلتر التوثيق */}
          <div>
            <select
              value={selectedVerified}
              onChange={e => setSelectedVerified(e.target.value)}
              className="block w-full border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            >
              <option value="all">الكل</option>
              <option value="verified">موثق</option>
              <option value="unverified">غير موثق</option>
            </select>
          </div>
        </div>
      </div>

      {/* جدول المستخدمين */}
      <div className="bg-white overflow-hidden shadow rounded-lg">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  المستخدم
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  النوع
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  الحالة
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  التوثيق
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  تاريخ التسجيل
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  آخر دخول
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  إجراءات
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {currentUsers.length > 0 ? (
                currentUsers.map(user => (
                  <tr key={user.id}>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10">
                          <img className="h-10 w-10 rounded-full" src={user.avatar} alt={user.name} />
                        </div>
                        <div className="mr-4">
                          <div className="text-sm font-medium text-gray-900">{user.name}</div>
                          <div className="text-sm text-gray-500">{user.email}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span
                        className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                          user.type === 'admin'
                            ? 'bg-purple-100 text-purple-800'
                            : user.type === 'provider'
                            ? 'bg-blue-100 text-blue-800'
                            : 'bg-green-100 text-green-800'
                        }`}
                      >
                        {user.type === 'admin' ? 'مدير' : user.type === 'provider' ? 'مزود خدمة' : 'عميل'}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span
                        className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                          user.status === 'نشط'
                            ? 'bg-green-100 text-green-800'
                            : user.status === 'معلق'
                            ? 'bg-yellow-100 text-yellow-800'
                            : 'bg-red-100 text-red-800'
                        }`}
                      >
                        {user.status}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {user.isVerified ? (
                        <CheckCircleIcon className="h-5 w-5 text-green-500" aria-hidden="true" />
                      ) : (
                        <XCircleIcon className="h-5 w-5 text-gray-400" aria-hidden="true" />
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {user.createdAt}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {user.lastLogin}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-left text-sm font-medium">
                      <div className="flex items-center space-x-3 space-x-reverse">
                        <button
                          type="button"
                          className="text-indigo-600 hover:text-indigo-900"
                        >
                          <PencilSquareIcon className="h-5 w-5" aria-hidden="true" />
                          <span className="sr-only">تعديل</span>
                        </button>
                        <button
                          type="button"
                          className="text-red-600 hover:text-red-900"
                        >
                          <TrashIcon className="h-5 w-5" aria-hidden="true" />
                          <span className="sr-only">حذف</span>
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={7} className="px-6 py-4 whitespace-nowrap text-sm text-gray-500 text-center">
                    لا توجد نتائج مطابقة للبحث
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* ترقيم الصفحات */}
      {filteredUsers.length > usersPerPage && (
        <div className="bg-white px-4 py-3 flex items-center justify-between border-t border-gray-200 sm:px-6">
          <div className="hidden sm:flex-1 sm:flex sm:items-center sm:justify-between">
            <div>
              <p className="text-sm text-gray-700">
                عرض <span className="font-medium">{indexOfFirstUser + 1}</span> إلى{' '}
                <span className="font-medium">
                  {Math.min(indexOfLastUser, filteredUsers.length)}
                </span>{' '}
                من <span className="font-medium">{filteredUsers.length}</span> مستخدم
              </p>
            </div>
            <div>
              <nav className="relative z-0 inline-flex rounded-md shadow-sm -space-x-px space-x-reverse" aria-label="Pagination">
                <button
                  onClick={goToPreviousPage}
                  disabled={currentPage === 1}
                  className={`relative inline-flex items-center px-2 py-2 rounded-r-md border border-gray-300 bg-white text-sm font-medium ${
                    currentPage === 1
                      ? 'text-gray-300 cursor-not-allowed'
                      : 'text-gray-500 hover:bg-gray-50'
                  }`}
                >
                  <span className="sr-only">السابق</span>
                  <svg
                    className="h-5 w-5"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                    aria-hidden="true"
                  >
                    <path
                      fillRule="evenodd"
                      d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
                {[...Array(totalPages)].map((_, i) => (
                  <button
                    key={i}
                    onClick={() => paginate(i + 1)}
                    className={`relative inline-flex items-center px-4 py-2 border text-sm font-medium ${
                      currentPage === i + 1
                        ? 'z-10 bg-indigo-50 border-indigo-500 text-indigo-600'
                        : 'bg-white border-gray-300 text-gray-500 hover:bg-gray-50'
                    }`}
                  >
                    {i + 1}
                  </button>
                ))}
                <button
                  onClick={goToNextPage}
                  disabled={currentPage === totalPages}
                  className={`relative inline-flex items-center px-2 py-2 rounded-l-md border border-gray-300 bg-white text-sm font-medium ${
                    currentPage === totalPages
                      ? 'text-gray-300 cursor-not-allowed'
                      : 'text-gray-500 hover:bg-gray-50'
                  }`}
                >
                  <span className="sr-only">التالي</span>
                  <svg
                    className="h-5 w-5"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                    aria-hidden="true"
                  >
                    <path
                      fillRule="evenodd"
                      d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                      clipRule="evenodd"
                    />
                  </svg>
                </button>
              </nav>
            </div>
          </div>
        </div>
      )}
    </div>
  );
} 