import React from 'react';
import { ArrowUpIcon, ArrowDownIcon } from '@heroicons/react/24/solid';

interface AdminStatsProps {
  title: string;
  count: number;
  growth: number;
  timeframe: string;
  bgColor: string;
  isCurrency?: boolean;
  icon: React.ComponentType<React.SVGProps<SVGSVGElement>>;
}

export default function AdminStats({
  title,
  count,
  growth,
  timeframe,
  bgColor,
  isCurrency = false,
  icon: Icon
}: AdminStatsProps) {
  
  // تنسيق العدد مع إضافة الفواصل للآلاف
  const formatNumber = (num: number) => {
    if (isCurrency) {
      return new Intl.NumberFormat('ar-SA', { 
        style: 'currency', 
        currency: 'SAR',
        maximumFractionDigits: 0 
      }).format(num);
    }
    return new Intl.NumberFormat('ar-SA').format(num);
  };

  return (
    <div className="bg-white overflow-hidden shadow rounded-lg">
      <div className="p-5">
        <div className="flex items-center">
          <div className={`flex-shrink-0 rounded-md p-3 ${bgColor}`}>
            <Icon className="h-6 w-6 text-white" aria-hidden="true" />
          </div>
          <div className="mr-5">
            <p className="text-sm font-medium text-gray-500 truncate">{title}</p>
            <p className="mt-1 text-xl font-semibold text-gray-900">{formatNumber(count)}</p>
          </div>
        </div>
      </div>
      <div className="bg-gray-50 px-5 py-3">
        <div className="flex items-center">
          {growth > 0 ? (
            <ArrowUpIcon className="h-5 w-5 text-green-500 ml-1" aria-hidden="true" />
          ) : (
            <ArrowDownIcon className="h-5 w-5 text-red-500 ml-1" aria-hidden="true" />
          )}
          <span
            className={`text-sm font-medium ${
              growth > 0 ? 'text-green-500' : 'text-red-500'
            }`}
          >
            {Math.abs(growth)}%
          </span>
          <span className="text-sm text-gray-500 mr-1">{timeframe}</span>
        </div>
      </div>
    </div>
  );
} 