'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import {
  UsersIcon, 
  ClipboardDocumentListIcon, 
  CreditCardIcon,
  DocumentCheckIcon,
  ShieldCheckIcon,
  ExclamationTriangleIcon,
  DocumentChartBarIcon,
  EnvelopeIcon
} from '@heroicons/react/24/outline';
import AdminStats from '@/components/dashboard/AdminStats';
import AdminTabContent from '@/components/dashboard/AdminTabContent';

// الألوان الخاصة بمختلف الإحصائيات
const statsColors = {
  users: 'bg-blue-500',
  providers: 'bg-purple-500',
  clients: 'bg-green-500',
  pendingVerifications: 'bg-yellow-500',
  orders: 'bg-indigo-500',
  revenue: 'bg-emerald-500',
  pendingWithdrawals: 'bg-orange-500',
  reports: 'bg-red-500',
};

// البيانات الافتراضية للعرض التوضيحي
const mockData = {
  users: { count: 1250, growth: 15, timeframe: 'الأسبوع الماضي' },
  providers: { count: 350, growth: 12, timeframe: 'الأسبوع الماضي' },
  clients: { count: 900, growth: 18, timeframe: 'الأسبوع الماضي' },
  pendingVerifications: { count: 28, growth: -5, timeframe: 'اليوم' },
  orders: { count: 780, growth: 24, timeframe: 'الشهر الحالي' },
  revenue: { count: 45600, growth: 32, timeframe: 'هذا الشهر', isCurrency: true },
  pendingWithdrawals: { count: 12, growth: 8, timeframe: 'اليوم' },
  reports: { count: 18, growth: -12, timeframe: 'هذا الأسبوع' },
};

export default function AdminDashboard() {
  const router = useRouter();
  const { user, isAdmin, loading } = useAuth();
  const [activeTab, setActiveTab] = useState('overview');
  const [isLoading, setIsLoading] = useState(true);
  const [stats, setStats] = useState(mockData);

  useEffect(() => {
    // التحقق من صلاحيات المستخدم
    if (!loading) {
      if (!user) {
        router.push('/login');
      } else if (!isAdmin) {
        router.push('/dashboard');
      } else {
        // محاكاة تأخير جلب البيانات
        const timer = setTimeout(() => {
          setIsLoading(false);
        }, 1000);
        return () => clearTimeout(timer);
      }
    }
  }, [user, isAdmin, loading, router]);

  const tabItems = [
    { id: 'overview', label: 'نظرة عامة', icon: DocumentChartBarIcon },
    { id: 'users', label: 'المستخدمين', icon: UsersIcon },
    { id: 'orders', label: 'الطلبات', icon: ClipboardDocumentListIcon },
    { id: 'services', label: 'الخدمات', icon: DocumentCheckIcon },
    { id: 'payments', label: 'المدفوعات', icon: CreditCardIcon },
    { id: 'withdrawals', label: 'طلبات السحب', icon: CreditCardIcon },
    { id: 'verification', label: 'طلبات التوثيق', icon: ShieldCheckIcon },
    { id: 'reports', label: 'البلاغات', icon: ExclamationTriangleIcon },
    { id: 'messages', label: 'الرسائل', icon: EnvelopeIcon },
  ];

  if (loading || isLoading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <div className="inline-flex items-center px-4 py-2 font-semibold leading-6 text-sm rounded-md text-white bg-indigo-500">
          <svg className="animate-spin -mr-1 ml-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
          جاري تحميل لوحة التحكم...
        </div>
      </div>
    );
  }

  return (
    <div className="py-6">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <h1 className="text-2xl font-semibold text-gray-900 mb-2">لوحة تحكم المدير</h1>
        <p className="text-gray-600 mb-6">إدارة المنصة ومراقبة الأداء العام</p>
        
        {/* الإحصائيات الرئيسية */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <AdminStats
            title="المستخدمين"
            count={stats.users.count}
            growth={stats.users.growth}
            timeframe={stats.users.timeframe}
            bgColor={statsColors.users}
            icon={UsersIcon}
          />
          <AdminStats
            title="مزودي الخدمات"
            count={stats.providers.count}
            growth={stats.providers.growth}
            timeframe={stats.providers.timeframe}
            bgColor={statsColors.providers}
            icon={UsersIcon}
          />
          <AdminStats
            title="الطلبات"
            count={stats.orders.count}
            growth={stats.orders.growth}
            timeframe={stats.orders.timeframe}
            bgColor={statsColors.orders}
            icon={ClipboardDocumentListIcon}
          />
          <AdminStats
            title="الإيرادات"
            count={stats.revenue.count}
            growth={stats.revenue.growth}
            timeframe={stats.revenue.timeframe}
            bgColor={statsColors.revenue}
            isCurrency={true}
            icon={CreditCardIcon}
          />
        </div>
        
        {/* علامات التبويب */}
        <div className="mb-4 border-b border-gray-200">
          <div className="flex overflow-x-auto whitespace-nowrap scrollbar-hide">
            {tabItems.map((item) => (
              <button
                key={item.id}
                onClick={() => setActiveTab(item.id)}
                className={`flex items-center px-4 py-3 text-sm font-medium ${
                  activeTab === item.id
                    ? 'border-b-2 border-indigo-600 text-indigo-600'
                    : 'border-b-2 border-transparent text-gray-600 hover:text-gray-800 hover:border-gray-300'
                }`}
              >
                <item.icon className="h-5 w-5 ml-2" />
                {item.label}
              </button>
            ))}
          </div>
        </div>
        
        {/* محتوى التبويب النشط */}
        <AdminTabContent activeTab={activeTab} />
      </div>
    </div>
  );
} 