import React, { useState, useEffect } from 'react';
import Overview from './admin-tabs/Overview';
import Users from './admin-tabs/Users';
import Orders from './admin-tabs/Orders';
import Services from './admin-tabs/Services';
import Payments from './admin-tabs/Payments';
import Withdrawals from './admin-tabs/Withdrawals';
import Verification from './admin-tabs/Verification';
import Reports from './admin-tabs/Reports';
import Messages from './admin-tabs/Messages';

interface AdminTabContentProps {
  activeTab: string;
}

export default function AdminTabContent({ activeTab }: AdminTabContentProps) {
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // محاكاة تحميل البيانات عند تغيير التبويب
    setIsLoading(true);
    const timer = setTimeout(() => {
      setIsLoading(false);
    }, 600);
    
    return () => clearTimeout(timer);
  }, [activeTab]);

  if (isLoading) {
    return (
      <div className="py-12 flex justify-center">
        <div className="inline-flex items-center">
          <svg className="animate-spin h-5 w-5 text-indigo-600 ml-3" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
          <span className="font-medium text-indigo-600">جاري تحميل البيانات...</span>
        </div>
      </div>
    );
  }

  // عرض محتوى التبويب النشط
  switch (activeTab) {
    case 'overview':
      return <Overview />;
    case 'users':
      return <Users />;
    case 'orders':
      return <Orders />;
    case 'services':
      return <Services />;
    case 'payments':
      return <Payments />;
    case 'withdrawals':
      return <Withdrawals />;
    case 'verification':
      return <Verification />;
    case 'reports':
      return <Reports />;
    case 'messages':
      return <Messages />;
    default:
      return (
        <div className="text-center py-12">
          <p className="text-gray-500">حدث خطأ في تحميل المحتوى</p>
        </div>
      );
  }
} 