import React, { useState } from 'react';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, BarElement, Title, Tooltip, Legend } from 'chart.js';
import { Bar, Line } from 'react-chartjs-2';

// تسجيل مكونات الرسم البياني
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend
);

// تحديد خيارات الرسم البياني الخطي
const lineOptions = {
  responsive: true,
  plugins: {
    legend: {
      position: 'top' as const,
    },
    title: {
      display: true,
      text: 'مؤشرات النمو الشهرية',
      font: {
        size: 16,
      }
    },
  },
  scales: {
    y: {
      beginAtZero: true,
    }
  }
};

// تحديد خيارات الرسم البياني الشريطي
const barOptions = {
  responsive: true,
  plugins: {
    legend: {
      position: 'top' as const,
    },
    title: {
      display: true,
      text: 'إحصائيات الفئات والإيرادات',
      font: {
        size: 16,
      }
    },
  },
  scales: {
    y: {
      beginAtZero: true,
    }
  }
};

// بيانات افتراضية للرسم البياني الخطي
const lineData = {
  labels: ['يناير', 'فبراير', 'مارس', 'أبريل', 'مايو', 'يونيو', 'يوليو', 'أغسطس', 'سبتمبر', 'أكتوبر', 'نوفمبر', 'ديسمبر'],
  datasets: [
    {
      label: 'مستخدمين جدد',
      data: [65, 78, 90, 115, 132, 150, 170, 180, 185, 205, 230, 240],
      borderColor: 'rgb(99, 102, 241)',
      backgroundColor: 'rgba(99, 102, 241, 0.5)',
      tension: 0.3,
    },
    {
      label: 'طلبات الخدمة',
      data: [50, 60, 70, 85, 95, 120, 150, 160, 180, 210, 240, 270],
      borderColor: 'rgb(16, 185, 129)',
      backgroundColor: 'rgba(16, 185, 129, 0.5)',
      tension: 0.3,
    },
    {
      label: 'الإيرادات (آلاف)',
      data: [20, 25, 28, 32, 38, 42, 50, 60, 65, 70, 80, 90],
      borderColor: 'rgb(249, 115, 22)',
      backgroundColor: 'rgba(249, 115, 22, 0.5)',
      tension: 0.3,
    },
  ],
};

// بيانات افتراضية للرسم البياني الشريطي
const barData = {
  labels: ['توليد الصور', 'كتابة المحتوى', 'برمجة', 'تحليل البيانات', 'معالجة اللغات', 'البوتات', 'غير ذلك'],
  datasets: [
    {
      label: 'عدد الخدمات',
      data: [80, 110, 75, 60, 90, 40, 30],
      backgroundColor: 'rgba(99, 102, 241, 0.7)',
    },
    {
      label: 'الإيرادات (آلاف)',
      data: [35, 50, 40, 30, 45, 20, 15],
      backgroundColor: 'rgba(249, 115, 22, 0.7)',
    },
  ],
};

// مكون بيانات الجدول
const tableData = [
  { id: 1, user: 'أحمد محمد', email: 'ahmed@example.com', type: 'مزود خدمة', registered: '2023/12/15', lastLogin: '2024/05/10', status: 'نشط' },
  { id: 2, user: 'سارة علي', email: 'sara@example.com', type: 'عميل', registered: '2024/01/23', lastLogin: '2024/05/12', status: 'نشط' },
  { id: 3, user: 'محمد أحمد', email: 'mohamed@example.com', type: 'مزود خدمة', registered: '2023/11/05', lastLogin: '2024/05/01', status: 'نشط' },
  { id: 4, user: 'فاطمة خالد', email: 'fatima@example.com', type: 'عميل', registered: '2024/02/10', lastLogin: '2024/05/08', status: 'نشط' },
  { id: 5, user: 'خالد عبدالله', email: 'khaled@example.com', type: 'مزود خدمة', registered: '2024/03/15', lastLogin: '2024/05/11', status: 'نشط' },
];

export default function Overview() {
  const [timeFrame, setTimeFrame] = useState('month');

  return (
    <div className="space-y-8">
      {/* شريط الفلتر والإحصائيات السريعة */}
      <div className="bg-white p-6 rounded-lg shadow mb-8">
        <div className="flex flex-wrap items-center justify-between mb-6">
          <h2 className="text-xl font-semibold text-gray-800">لوحة المؤشرات</h2>
          
          <div className="inline-flex items-center rounded-md">
            <button
              onClick={() => setTimeFrame('week')}
              className={`px-4 py-2 text-sm font-medium rounded-r-md ${
                timeFrame === 'week'
                  ? 'bg-indigo-600 text-white hover:bg-indigo-700'
                  : 'bg-white text-gray-700 border border-gray-300 hover:bg-gray-50'
              }`}
            >
              أسبوعي
            </button>
            <button
              onClick={() => setTimeFrame('month')}
              className={`px-4 py-2 text-sm font-medium ${
                timeFrame === 'month'
                  ? 'bg-indigo-600 text-white hover:bg-indigo-700'
                  : 'bg-white text-gray-700 border-t border-b border-gray-300 hover:bg-gray-50'
              }`}
            >
              شهري
            </button>
            <button
              onClick={() => setTimeFrame('year')}
              className={`px-4 py-2 text-sm font-medium rounded-l-md ${
                timeFrame === 'year'
                  ? 'bg-indigo-600 text-white hover:bg-indigo-700'
                  : 'bg-white text-gray-700 border border-gray-300 hover:bg-gray-50'
              }`}
            >
              سنوي
            </button>
          </div>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="h-80">
            <Line options={lineOptions} data={lineData} />
          </div>
          <div className="h-80">
            <Bar options={barOptions} data={barData} />
          </div>
        </div>
      </div>
      
      {/* المستخدمين النشطين مؤخراً */}
      <div className="bg-white p-6 rounded-lg shadow mb-8">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-lg font-semibold text-gray-800">المستخدمين النشطين مؤخراً</h2>
          <button className="text-sm text-indigo-600 hover:text-indigo-800">
            عرض الكل
          </button>
        </div>
        
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  المستخدم
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  البريد الإلكتروني
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  النوع
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  تاريخ التسجيل
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  آخر تسجيل دخول
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  الحالة
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {tableData.map((row) => (
                <tr key={row.id}>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm font-medium text-gray-900">{row.user}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-500">{row.email}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-500">{row.type}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-500">{row.registered}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-500">{row.lastLogin}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
                      {row.status}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
      
      {/* الإشعارات المهمة */}
      <div className="bg-white p-6 rounded-lg shadow">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-lg font-semibold text-gray-800">الإشعارات المهمة</h2>
        </div>
        
        <div className="space-y-4">
          <div className="border-r-4 border-yellow-500 bg-yellow-50 p-4 rounded">
            <div className="flex">
              <div className="flex-shrink-0">
                <svg className="h-5 w-5 text-yellow-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                </svg>
              </div>
              <div className="mr-3">
                <h3 className="text-sm font-medium text-yellow-800">يوجد 5 طلبات توثيق جديدة بحاجة للمراجعة</h3>
                <div className="mt-1 text-sm text-yellow-700">
                  <p>يرجى مراجعة طلبات التوثيق في أقرب وقت ممكن.</p>
                </div>
                <div className="mt-2">
                  <button className="text-sm font-medium text-yellow-800 hover:text-yellow-900">
                    عرض الطلبات
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="border-r-4 border-red-500 bg-red-50 p-4 rounded">
            <div className="flex">
              <div className="flex-shrink-0">
                <svg className="h-5 w-5 text-red-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 1.944A11.954 11.954 0 012.166 5C2.056 5.649 2 6.319 2 7c0 5.225 3.34 9.67 8 11.317C14.66 16.67 18 12.225 18 7c0-.682-.057-1.35-.166-2.001A11.954 11.954 0 0110 1.944zM11 14a1 1 0 11-2 0 1 1 0 012 0zm0-7a1 1 0 10-2 0v3a1 1 0 102 0V7z" clipRule="evenodd" />
                </svg>
              </div>
              <div className="mr-3">
                <h3 className="text-sm font-medium text-red-800">يوجد 3 بلاغات جديدة ضد خدمات</h3>
                <div className="mt-1 text-sm text-red-700">
                  <p>قم بمراجعة البلاغات والتحقق من المخالفات المحتملة.</p>
                </div>
                <div className="mt-2">
                  <button className="text-sm font-medium text-red-800 hover:text-red-900">
                    عرض البلاغات
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 